import React from 'react'
import Slider from "react-slick"


export default function CardsWork() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
          breakpoint: 1100,
          settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              infinite: true,
              dots: true,
          }


      },
      {
          breakpoint: 640,
          settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
          }


      }
  ]

  };


  return (
    <div className='CardsWork'>
      <div className="CardsWork_title">Why work with us</div>

      <div className="CardsWork_card">
        <Slider {...settings}>
          <div className="CardsWork_itmes">
            <button>Lorem ipsum</button>
            <h3>Lorem ipsum</h3>
            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
          </div>
          <div className="CardsWork_itmes">
            <button>Lorem ipsum</button>
            <h3>Lorem ipsum</h3>
            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
          </div>
          <div className="CardsWork_itmes">
            <button>Lorem ipsum</button>
            <h3>Lorem ipsum</h3>
            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
          </div>
          <div className="CardsWork_itmes">
            <button>Lorem ipsum</button>
            <h3>Lorem ipsum</h3>
            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
          </div>



        </Slider>


      </div>




    </div>
  )
}

