import React, { useState } from 'react'
import Slider from "react-slick"
// import CardsTeam_img1 from '../Imges/CardsTeam_img1.png'
// import CardsTeam_img2 from '../Imges/CardsTeam_img2.png'
// import CardsTeam_img3 from '../Imges/CardsTeam_img3.png'
// import CardsTeam_img4 from '../Imges/CardsTeam_img4.png'
import Data from '../CardsTeam.json'

export default function CardsTeam() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 970,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        }


      },
      {
        breakpoint: 710,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        }


      },
      {
        breakpoint: 420,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        }


      }
    ]
  };
  let [data, setdata] = useState(Data.CardsTeam);



  return (
    <div className='CardsTeam'>
      <div className="CardsTeam_title">Our Team</div>
      <div className='CardsTeam_card'>
        <Slider {...settings}>

          {data.map((value, key) => (
            <div key={key} className="CardsTeam_card_item">
              <img src={value.img} alt="" />
              <h6>{value.title}</h6>
              <p>coo</p>
            </div>

          ))}

        </Slider>
      </div>




    </div>
  )

}



