import React from 'react'
import Unity from '../Imges/unity.png'
import Cry from '../Imges/cry.png'
import Unreal from '../Imges/unreal.png'

export default function Banner() {
  return (
    <div className='homeBanner'>
      <div className="Home_col">
        <h3>3D game Dev </h3>
        <h1>Work that we <br /> produce for our <br /> br clients</h1>
        <p>Lorem Ipsum is simply dummy text of the printing and <br /> typesetting industry. Lorem Ipsum has been the industry's standard.</p>
        <button>Get more details</button>
      </div>
      <div className="Home_col">
        <img src={Unity} alt="" />
        <img src={Cry} alt="" />
        <img src={Unreal} alt="" />

      </div>
    </div>
  )
}
