import React from 'react'
import './Sass/Userloop.scss'

export default function Userloop() {
  return (
    <div className='Userloop'>
      <div className="Userloop_row_1">
        <h2>Lorem Ipsum</h2>
        <p>Lorem Ipsum is simply dummy text of the printing and typesetting <br /> industry. </p>
      </div>
      <div className="Userloop_row_2">
        <div className="Userloop_row_2_col">
          <h3>Stay in the loop</h3>
          <p>Subscribe to receive the latest news and updates about TDA. <br /> 
            We promise not to spam you! </p>
        </div>
        <div className="Userloop_row_2_col">
          <div className="Userloop_row_2_col_input">
            <input type="text" name="text" id="text" placeholder='Enter email address' />
            <button>Continue</button>
          </div>


        </div>
      </div>
    </div>
  )
}
