import React from 'react'
import Titletext from '../Titletext/Titletext'
import Contact_c from './Components/Contact_c/Contact_c'
import Contact_log from './Components/Contact_log/Contact_log'
import Userloop from '../Userloop/Userloop'

import './Sass/Contact.scss'

export default function Contact() {
  return (
    <div className='Contact'>
      <Titletext />
      <Contact_c />
      <Contact_log /> 
      <Userloop />
    </div>
  )
}
