import React from 'react'

export default function Contact_log() {
  return (
    <div className='Contact_log'>
      <div className="Contact_log_box">
        <div className="Contact_log_title">
          <h3>Say hello</h3>
          <p>Lorem Ipsum is simply dummy text of the printing .</p>
        </div>
        <div className="Contact_log_input">
          <div className="Contact_log_input_row">
            <div className="Contact_log_input_row_col">
              <label htmlFor="name">First Name</label>
              <input type="text" name="text" id="name" />
            </div>
            <div className="Contact_log_input_row_col">
              <label htmlFor="lastname">Last Name</label>
              <input type="text" name="text" id="lastname" />

            </div>

          </div>
          <label htmlFor="email">Email Address</label>
          <input type="text" name='email' id='email' />
          <label htmlFor="message">Message</label>
          <textarea name="message" id="message" cols="30" rows="10"></textarea>
          <button>Get in touch</button>



        </div>
      </div>

    </div>
  )
}
